<template>
  <div class="form-wrap">
    <div class="form-info">
      <h2>수정</h2>
      <loading v-if="$store.state.loading"></loading>
      <form @submit.prevent="submitForm" v-else>
        <div>
          <label for="title">제목</label>
          <input type="text" id="title" v-model="title" />
        </div>
        <div>
          <label for="contents">내용</label>
          <textarea type="text" id="contents" v-model="contents" rows="5" />
        </div>
        <button type="submit">수정하기</button>
      </form>
    </div>
  </div>
</template>

<script>
import { editPost, fetchList } from "@/api/posts";
import Loading from "./common/Loading.vue";
export default {
  components: { Loading },
  data() {
    return {
      title: "",
      contents: "",
      create: "",
    };
  },
  async created() {
    this.$store.state.loading = true;
    const { data } = await fetchList(this.$route.params.id);
    this.$store.state.loading = false;
    this.title = data.title;
    this.contents = data.contents;
    this.create = data.createdAt;
  },
  methods: {
    async submitForm() {
      try {
        const data = {
          title: this.title,
          contents: this.contents,
        };
        const response = await editPost(this.$route.params.id, data);
        console.log(response);
        this.$router.push("/main");
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>

<style scoped lang="scss">
.form-wrap {
  display: flex;
  justify-content: center;

  .form-info {
    width: 100%;
    background: #fff;
    padding: 2%;
    border-radius: 10px;
    h2 {
      text-align: center;
    }
    a {
      color: #000;
    }
    button {
      margin: 30px 0 10px;
      width: 100%;
      padding: 10px;
      transition: all 0.1s ease-in-out;
      background: #ff8f00;
      color: #fff;
      &:hover {
        background: #ffd54f;
      }
    }
    input {
      margin: 5px;
    }
  }
}
</style>
