import { instanceAuth } from "./";

function fetchLists() {
  return instanceAuth.get("/");
}

function createPost(data) {
  return instanceAuth.post("/", data);
}

function removeItem(id) {
  return instanceAuth.delete(id);
}

function fetchList(id) {
  return instanceAuth.get(id);
}

function editPost(id, data) {
  return instanceAuth.put(id, data);
}

export { fetchLists, createPost, removeItem, fetchList, editPost };
